body, html {
  margin: 0;
  background-color: #16171D;
  background-color: var(--body);
  min-height: 100vh;
}

body, html, input, button {
  font-family: 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1rem;
}
.feather.left {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.arrow {
    display: inline-block;
}

.card {
    background-color: var(--card);
    padding: 3rem 4rem;
    margin: 0 3rem;
    margin-bottom: 6rem;
    border-radius: 10px;
    box-sizing: border-box;
    transition: height 500ms ease-in-out;
}

.card-wrapper.full {
    width: 81rem;
}

.card-wrapper {
  transition: height 500ms ease-in-out;
  width: 45rem;
  max-width: 100%;
}

.card-wrapper.overflow-hidden {
    overflow: hidden;
}

@media screen and (max-width: 700px) {
    .card {
        margin: 0;
        margin-bottom: 6rem;
        padding: 3rem 2rem;
    }
}
.errorBanner {
	margin-top: 0.5rem;
    -webkit-border-start: none;
            border-inline-start: none;
    font-size: 16px;
    font-weight: normal;
    letter-spacing: -.01em;
    padding: .5rem 1rem .5rem .75rem;
    border-radius: .25rem;
	background-color: var(--button);
	color: var(--button-text);
}
.inputBar {
    width: 100%;
    display: flex;
    height: 3rem;
}

.inputBar > *:first-child{
    border-radius: 0 !important;
    border-top-left-radius: 10px !important;
    border-bottom-left-radius: 10px !important;
}

.inputBar > *:last-child {
    border-radius: 0 !important;
    border-top-right-radius: 10px !important;
    border-bottom-right-radius: 10px !important;
}

.inputTextBox {
    border-width: 0;
    outline: none;
    background-color: var(--content);
    color: var(--text);
    padding: .7rem 1.5rem;
    height: auto;
    flex: 1 1;
    box-sizing: border-box;
}

.inputSearchButton {
    background-color: var(--button);
    border-width: 0;
    color: var(--text);
    color: var(--button-text, var(--text));
    padding: .5rem 2.1rem;

    font-weight: bold;
    cursor: pointer;
}

.inputSearchButton:hover {
    background-color: var(--button-hover);
}

.inputTextBox:hover {
    background-color: var(--content-hover);
}

.inputSearchButton .text > .arrow {
    opacity: 0;
    transition: opacity 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
    transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;
    transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
    position: absolute;
    right: -0.8rem;
    bottom: -0.2rem;
}

.inputSearchButton .text {
    display: flex;
    position: relative;
    transition: -webkit-transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

.inputSearchButton:hover .text > .arrow {
    -webkit-transform: translateX(8px);
            transform: translateX(8px);
    opacity: 1;
}

.inputSearchButton:hover .text {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
}

.inputSearchButton:active {
    background-color: var(--button-active);
}

@media screen and (max-width: 700px) {
    .inputBar {
        flex-direction: column;
        align-items: flex-start;
        height: auto;
    }

    .inputBar > *:nth-child(n) {
        border-radius: 10px !important;
    }

    .inputSearchButton {
        margin-top: .5rem;
        align-self: center;
    }

    .inputTextBox {
        margin-top: .5rem;
        width: 100%;
    }
}

.progressBar {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.2;
}
.progressBarInner {
    background: var(--theme-color);
    height: 100%;
}
.movieRow {
    position: relative;
    display: flex;
    border-radius: 5px;
    background-color: var(--content);
    color: var(--text);
    padding: .8rem 1.5rem;
    margin-top: .5rem;
    cursor: pointer;
    transition: -webkit-transform 50ms ease-in-out;
    transition: transform 50ms ease-in-out;
    transition: transform 50ms ease-in-out, -webkit-transform 50ms ease-in-out;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    overflow: hidden;
}

.movieRow p {
    margin: 0;
}

.movieRow .left {
    flex: 1 1;
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start;
    margin-right: 0.5rem;
}

.movieRow .left .titleWrapper {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.movieRow .left .seasonEpisodeSubtitle,
.movieRow .left .year {
    color: var(--text-secondary);
}

.movieRow .watch {
    color: var(--theme-color-text);
    display: flex;
    align-items: center;
}

.movieRow .watch .arrow {
    margin-left: .5rem;
    transition: -webkit-transform 50ms ease-in-out;
    transition: transform 50ms ease-in-out;
    transition: transform 50ms ease-in-out, -webkit-transform 50ms ease-in-out;
    -webkit-transform: translateY(.1rem);
            transform: translateY(.1rem);
}

.movieRow:active {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

.movieRow:hover {
    background-color: var(--content-hover);
}

.movieRow:hover .watch .arrow {
    -webkit-transform: translateX(.3rem) translateY(.1rem);
            transform: translateX(.3rem) translateY(.1rem);
}

.attribute {
    color: var(--text);
    background-color: var(--theme-color);
    font-size: .75rem;
    padding: .25rem;
    border-radius: 10px;
    margin-right: 10px;
}

.subtitleIcon {
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
}

@media screen and (max-width: 400px) {
    .movieRow {
        flex-direction: column;
    }

    .movieRow .watch {
        margin-top: .5rem;
    }
}

.progress {
    text-align: center;
    color: var(--text-secondary);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 5rem;
    margin-top: 1rem;
    transition: height 800ms ease-in-out, opacity 800ms ease-in-out;
    opacity: 1;
}

.progress.hide {
    opacity: 0;
    height: 0rem;
}

.progress p {
    margin: 0;
    margin-bottom: 1rem;
}

.progress .bar {
    width: 13rem;
    max-width: 100%;
    background-color: var(--content);
    border-radius: 10px;
    height: 7px;
    display: inline-block;
}

.progress .bar .bar-inner {
    transition: width 400ms ease-in-out, background-color 100ms ease-in-out;
    background-color: var(--theme-color);
    border-radius: 10px;
    height: 100%;
    width: 0%;
}

.progress.failed .bar .bar-inner {
    background-color: var(--failed);
}

.title {
    font-size: 2rem;
    color: var(--text);
    /* max-width: 20rem; */
    margin: 0;
    padding: 0;
    margin-bottom: 3.5rem;
}

.title-size-medium {
    font-size: 1.5rem;
}
.title-size-small {
    font-size: 1.1rem;
    color: var(--text-secondary);
}

.title-accent {
    color: var(--theme-color);
    font-weight: 600;
    margin: 0;
    padding: 0;
    margin-bottom: 0.5rem;
    margin-top: 1rem;
    display: inline-block;
}

.title-accent.title-accent-link {
    cursor: pointer;
}

.title-accent.title-accent-link .arrow {
    transition: -webkit-transform 100ms ease-in-out;
    transition: transform 100ms ease-in-out;
    transition: transform 100ms ease-in-out, -webkit-transform 100ms ease-in-out;
    -webkit-transform: translateY(.1rem);
            transform: translateY(.1rem);
    margin-right: .2rem;
}

.title-accent.title-accent-link:hover .arrow {
    -webkit-transform: translateY(.1rem) translateX(-.5rem);
            transform: translateY(.1rem) translateX(-.5rem);
}



/* TODO better responsiveness, use dropdown if more than 5 options */
.typeSelector {
    display: inline-flex;
    position: relative;
    margin-bottom: 1.5rem;
    max-width: 100%;
}
.typeSelector:not(.nowrap) {
    flex-wrap: wrap;
}

.typeSelector::before {
    content: "";
    position: absolute;
    width: 100%;
    bottom: 0;
    background-color: var(--content);
    height: 4px;
    border-radius: 2px;
}

.typeSelector .choice {
    width: 7rem;
    height: 3rem;
    padding: .3rem .2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    box-sizing: border-box;
    color: var(--text-tertiary);
    font-weight: bold;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.typeSelector .choice:hover { 
    color: var(--text-secondary);
}

.typeSelector .choice.selected {
    color: var(--text);
}

.typeSelector .selectedBar {
    position: absolute;
    height: 4px;
    width: 7rem;
    background-color: var(--theme-color);
    border-radius: 2px;
    bottom: 0;
    transition: -webkit-transform 150ms ease-in-out;
    transition: transform 150ms ease-in-out;
    transition: transform 150ms ease-in-out, -webkit-transform 150ms ease-in-out;
}

@media screen and (max-width: 700px) {
    .typeSelector:not(.nowrap) {
        display: block;
    }
}

.cardView {
    display: flex;
    min-height: 100vh;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 1rem;
    box-sizing: border-box;
}

.cardView nav {
    width: 100%;
    max-width: 624px;
}
.cardView nav span {
    padding: 8px 16px;
    margin-right: 10px;
    border-radius: 4px;
    color: var(--text);
}
.cardView nav span:not(.selected-link) {
    cursor: pointer;
}
.cardView nav span.selected-link {
    background: var(--card);
    color: var(--button-text);
    font-weight: bold;
}

.cardView > * {
    margin-top: 2rem;
}

.cardView > *:first-child {
    margin-top: 38px;
}

.topRightCredits {
    position: absolute;
    right: 1rem;
    top: 1rem;
    margin-top: 0 !important;
    text-align: right;
}

.topRightCredits a, .topRightCredits a:visited {
    color: var(--theme-color);
    text-decoration: none;
    margin: 0;
}

.topRightCredits a:hover, .topRightCredits a:active {
    color: var(--theme-color);
    text-decoration: none;
}

.topRightCredits a .arrow {
    -webkit-transform: translateY(.1rem);
            transform: translateY(.1rem);
}

.topRightCredits a:hover .arrow {
    -webkit-transform: translateY(.1rem) translateX(.2rem);
            transform: translateY(.1rem) translateX(.2rem);
}

p.source {
    text-transform: uppercase;
    font-weight: bold;
    color: var(--source-headings);
    font-size: 0.8em;
    margin-top: 2rem;
}

.videoPlaceholder {
	width: 100%;
	position: relative;
}
.videoPlaceholder::before {
	content: '';
	display: block;
	width: 100%;
	padding-bottom: 56.25%;
}
.videoPlaceholderBox {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	position: absolute;
	background: var(--choice);
	border-radius: 6px;
	color: var(--text);
}
.videoElement {
    width: 100%;
    background-color: black;
    border-radius: 5px;
}

.videoElementText {
    color: var(--text);
    margin: 0;
}

.numberSelector {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(2.5rem, 1fr));
    grid-gap: 5px;
    gap: 5px;
    position: relative;
    margin-bottom: 1.5rem;
}

.numberSelector .choiceWrapper {
    position: relative;
    border-radius: 10%;
    overflow: hidden;
}

.numberSelector .choiceWrapper::before {
    content: '';
    display: block;
    width: 100%;
    padding-bottom: 100%;
}

.numberSelector .choice {
    position: absolute;
    top: 0; 
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--choice);
    margin-right: 5px;
    padding: .2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: var(--text);
    font-weight: bold;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    box-sizing: border-box;
}

.numberSelector .choice:hover { 
    background-color: var(--choice-hover);
}

.numberSelector .choice.selected {
    color: var(--text);
    color: var(--choice-active-text, var(--text));
    background-color: var(--choice-active);
}


/* select box styling */
.select-box {
  display: flex;
  width: 200px;
  flex-direction: column;
	position: relative;
}

.select-box > * {
	box-sizing: border-box;
}

.select-box .options-container {
  max-height: 0;
  width: calc( 100% - 12px);
  opacity: 0;
  transition: all 0.2s ease-in-out;
  overflow: hidden;
  border-radius: 5px;
  background-color: var(--choice);
  order: 1;
	position: absolute;
  z-index: 1;
	top: 50px;
}

.select-box .selected {
  margin-bottom: 8px;
  position: relative;
  width: 188px;
  height: 45px;
  border-radius: 5px;
	display: flex;
	align-items: center;
  background-color: var(--choice);
	color: white;
  order: 0;
}

.select-box .selected::after {
  content: "";
  width: 1.2rem;
  height: 1.2rem;
  background: url(/static/media/down-arrow.8666172a.svg);
  position: absolute;
  right: 15px;
  top: 50%;
  transition: -webkit-transform 150ms;
  transition: transform 150ms;
  transition: transform 150ms, -webkit-transform 150ms;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-size: contain;
  background-position: center;
}


.select-box .option .item {
	color: var(--text);
  font-weight: bold;
}

.select-box .options-container.active {
  max-height: 240px;
  opacity: 1;
  overflow-y: scroll;
}

.select-box .options-container.active + .selected::after {
  -webkit-transform: translateY(-50%) rotateX(180deg);
          transform: translateY(-50%) rotateX(180deg);
}

.select-box .options-container::-webkit-scrollbar {
  width: 8px;
  background: #0d141f;
  background: #81878f;
  background: #f1f2f3;
  border-radius: 0 5px 5px 0;
}

.select-box .options-container::-webkit-scrollbar-thumb {
  background: #525861;
  background: #81878f;
  border-radius: 0 5px 5px 0;
}
.select-box .option {
  padding: 12px 15px;
}

.select-box .option,
.selected {
  cursor: pointer;
}

.select-box .options-container .option:hover {
  background: var(--choice-hover);
}
.select-box .options-container .option:hover .item {
	color: var(--text);
	color: var(--choice-active-text, var(--text));
}

.select-box label {
  cursor: pointer;
}

.select-box .option .radio {
  display: none;
}

.episodeSelector {
	margin-top: 20px;
}
.showType-show .title-size-big {
	margin-bottom: 10px;
}
.showType-show .title-size-small, .showType-movie .title-size-big {
	margin-bottom: 30px;
}
